.nav p {
  margin: 0 15px 0 0; }

.nav .information {
  height: 100%;
  padding: 0 15px; }
  .nav .information p {
    line-height: 0.6;
    margin: 15px 0; }

.nav .nav-wrapper .dropdown-content a {
  background: #ee6e73;
  color: white; }
  .nav .nav-wrapper .dropdown-content a:hover {
    background: #e5535e; }

@media screen and (max-width: 700px) {
  .nav p, .nav a {
    font-size: 12px;
    padding: 0 5px; } }

@media screen and (max-width: 600px) {
  .nav p, .nav a {
    margin: 0 5px;
    padding: 0; } }



